import "./App.css";
import LineGraph from "./Components/Graphs/LineGraph";
import PieChart from "./Components/Graphs/PieChart";
import GetData from "./Utils/apiHandler";
import Header from "./Components/Header";
import GeneralInfo from "./Components/GeneralInfo";
import SettingsModal from "./Components/SettingsModal";
import { useEffect, useState } from "react";
import Devices from "./Components/Graphs/Devices";
import { AiOutlinePoweroff } from "react-icons/ai";
import axios from "axios";

function App() {
  const [user, setUser] = useState({});
  const [devices, setDevices] = useState([]);
  const [currentDevice, setCurrentDevice] = useState({});
  const [modal, setModal] = useState(false);

  useEffect(() => {
    GetData(setUser, setDevices);
  }, []);

  const changePowerState = async (e) => {
    try {
      const res = await axios.post(
        "/api/setpower",
        {
          state: currentDevice.relaypower ? 0 : 1,
          id: currentDevice.id,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("authtoken")}`,
          },
        }
      );
      GetData(setUser, setDevices);
      currentDevice.relaypower = currentDevice.relaypower ? 0 : 1;
    } catch (err) {
      console.log(err);
    }
  };
  return (
    <div className="container">
      <div
        onClick={() => setModal(false)}
        className="modalOverlay"
        style={modal ? {} : { display: "none" }}
      ></div>
      <div
        className="settingsmodalhome"
        style={modal ? {} : { display: "none" }}
      >
        <SettingsModal user={user} setModal={setModal} />
      </div>
      <div className="Pie">
        <PieChart
          data={devices.map((device) => {
            const power = device.archive[device.archive.length - 1]
              ? device.archive[device.archive.length - 1].power
              : "No Data";
            return { id: device.name, label: device.name, value: power };
          })}
        />
      </div>
      <div className="Header">
        <Header setModal={setModal} />
      </div>
      <div className="Time-Selector">
        <div onClick={changePowerState}>
          {currentDevice.name ? (
            <AiOutlinePoweroff
              style={{
                backgroundColor: currentDevice.relaypower ? "green" : "red",
              }}
              className="powericon"
            />
          ) : null}
        </div>
        <h1 className="CurrentNameHeader">{currentDevice.name}</h1>
      </div>
      <div className="General-Info">
        <GeneralInfo
          price={user.energyprice}
          device={currentDevice.color ? currentDevice : "test"}
        />
      </div>
      <div className="Graph">
        {currentDevice.archive ? (
          <LineGraph
            data={[
              {
                id: currentDevice.name,
                data: currentDevice.archive.map((time) => {
                  const x = new Date(time.date).getSeconds();
                  return { x, y: time.power };
                }),
              },
            ]}
            color={currentDevice.color}
          />
        ) : (
          []
        )}
      </div>
      <div className="Devices">
        <Devices
          devices={devices}
          setCurrentDevice={setCurrentDevice}
          currentDevice={currentDevice}
        />
      </div>
      <div className="Postal-Info">
        <h1></h1>
      </div>
      <div className="Footer">
        <div
          style={{
            width: "100%",
            height: "100%",
            backgroundColor: "#34495e",
            color: "#ffffff",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          © Copyright by Power Measurement Unit
        </div>
      </div>
    </div>
  );
}

export default App;
