import React from "react";
import RangeBar from "./Graphs/RangeBar";
import "./Graphs/RangeBar.css";

const GeneralInfo = function (props) {
  if (props.device === "test") {
    return (
      <div className="general-box">
        <h1 style={{ fontSize: "3rem" }}>Please Select A Device</h1>
      </div>
    );
  }
  try {
    return (
      <div className="general-box">
        <div className="text-items">
          <h1 className="kosten-test">
            Power State: {props.device.relaypower ? "ON" : "OFF"}
          </h1>
          <h1 className="kosten-test">
            Verbruik:{" "}
            {props.device.archive[props.device.archive.length - 1].power} W
          </h1>
          <h1 className="kosten-test">
            Kosten:{" "}
            {props.price
              ? (
                  props.price *
                  (props.device.archive[props.device.archive.length - 1].power /
                    1000)
                ).toFixed(2)
              : "No Energyprice"}{" "}
            €
          </h1>
          <h1 className="kosten-test">
            Uitstoot:{" "}
            {(
              0.649 *
              (props.device.archive[props.device.archive.length - 1].power /
                1000)
            ).toFixed(2)}{" "}
            kg
          </h1>
        </div>
        <div className="range-bar-box">
          <RangeBar marker={props.device.efficiency} />
        </div>
      </div>
    );
  } catch (error) {
    return (
      <div className="general-box">
        <h1 style={{ fontSize: "3rem" }}>No Data on device</h1>
      </div>
    );
  }
};

export default GeneralInfo;
