import React, { useEffect, useState } from "react";
import "./SettingsModal.css";
import {
  FiX,
  FiMail,
  FiMapPin,
  FiCreditCard,
  FiLock,
  FiXCircle,
} from "react-icons/fi";
import axios from "axios";

const SettingsModal = function (props) {
  const [email, setEmail] = useState("");
  const [postalcode, setPostalcode] = useState("");
  const [energyprice, setEnergyprice] = useState("");

  //   useEffect(() => {
  //     setEmail(user.email);
  //     setPostalcode(user.postalcode);
  //     setEnergyprice(user.energyprice);
  //   });

  const onChangeSubmit = async () => {
    try {
      if (email) {
        const res = await axios.patch(
          "/api/change/email",
          {
            email,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("authtoken")}`,
            },
          }
        );
      }
      if (postalcode) {
        const res2 = await axios.patch(
          "/api/change/postalcode",
          {
            postalcode,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("authtoken")}`,
            },
          }
        );
      }
      if (energyprice) {
        console.log(energyprice);
        const res3 = await axios.patch(
          "/api/change/energyprice",
          {
            energyprice,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("authtoken")}`,
            },
          }
        );
      }
      props.setModal(false);
    } catch (err) {
      console.log(err);
    }
  };

  const changePasswordHandler = async () => {
    console.log(props.user.email);
    try {
      const res = await axios.post("/api/forgotpassword", {
        email: props.user.email,
      });
      localStorage.removeItem("authtoken");
      window.location = "/login";
    } catch (err) {
      console.log(err);
    }
  };
  const deleteAccountHandler = async () => {
    try {
      const res = await axios.delete("/api/change/deleteaccount", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("authtoken")}`,
        },
      });
      localStorage.removeItem("authtoken");
      window.location = "/";
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <div className="background">
      <div className="bar1">
        <div className="head">Personal Information</div>
        <FiX onClick={() => props.setModal(false)} className="close" />
      </div>
      <div className="bar2">
        <FiMail style={{ width: "28px", height: "28px", marginTop: "1%" }} />
        <div className="text">Email</div>
      </div>
      <div className="bar3">
        <input
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className="inputEmail"
        />
      </div>

      <div className="bar4">
        <div className="postal">
          <FiMapPin className="icon" />
          <div className="text">Postal Code</div>
        </div>

        <div className="energy">
          <FiCreditCard className="icon" />
          <div className="text">Energy Price</div>
        </div>
      </div>

      <div className="bar5">
        <div className="bar5div">
          <input
            value={postalcode}
            onChange={(e) => setPostalcode(e.target.value)}
            className="bar5input"
          />
        </div>
        <div className="bar5div">
          <input
            value={energyprice}
            onChange={(e) => setEnergyprice(e.target.value)}
            className="bar5input"
          />
        </div>
      </div>

      <button onClick={onChangeSubmit} className="savebutton">
        Change
      </button>

      <button onClick={changePasswordHandler} className="changepassword">
        <FiLock style={{ fontSize: "24px" }} className="lock" />
        <div style={{ fontSize: "24px" }}>Change password</div>
      </button>

      <button onClick={deleteAccountHandler} className="deleteaccount">
        <FiXCircle style={{ fontSize: "24px" }} className="xcircle" />
        <div style={{ fontSize: "24px" }}>Delete account</div>
      </button>
    </div>
  );
};

export default SettingsModal;
